import React, { useEffect, useState, useRef } from 'react'
import BaseLayout from '../commonlayout/BaseLayout'
import TextField from '../common/TextField'
import SettingSidebar from './SettingSidebar'
import axios from 'axios'
import { API_URL, BlockPopup, IMAGE_URL, ToastPopup, uploadFileServer } from '../../actions/constant'
import { useDispatch } from 'react-redux'
import { listUserSetting } from '../../reducers/usersettingReducer'

const Setting = () => {
    const fileInputRef = useRef(null);
    const initData = {

    }

    const dispatch = useDispatch()

    const [data, setdata] = useState(initData)
    const [errors, seterrors] = useState(initData)



    const handleFileChange = async (event) => {
        const selectedFile = event.target.files[0];
        if (event.target.files) {
            const formdata = new FormData()
            formdata.append("file", event.target.files[0])
            formdata.append("filename", event.target.files[0].name)

            try {
                const filename = await uploadFileServer(formdata)
                setdata({ ...data, invoiceLogo: filename })
            } catch (e) {
                ToastPopup.failure("Error Ocuured in File Upload")
            }
        }

        // Do something with the selected file
        console.log('Selected file:', selectedFile);
    };


    useEffect(() => {
        getData()
    }, [])

    const getData = async () => {
        BlockPopup.standard('.invoicesetting', `Please wait... Don't Close`);
        try {
            let result = await axios.get(API_URL + "/api/usersetting/")
            BlockPopup.remove('.invoicesetting');
            if (result.data) {
                setdata(result.data)
            }

        } catch (err) {
            BlockPopup.remove('.invoicesetting');
            ToastPopup.failure("Error Ocuured in API")
        }
    }

    const onSubmit = async (e) => {
        e.preventDefault()
        BlockPopup.standard('.invoicesetting', `Please wait... Don't Close`);

        try {
            let result = await axios.post(API_URL + "/api/usersetting/", data)
            if (result.data) {
                BlockPopup.remove('.invoicesetting');
                ToastPopup.success("Successfully Added")
                dispatch(listUserSetting());
            }

        } catch (err) {
            BlockPopup.remove('.invoicesetting');
            if (err && err.response && err.response.data) {
                seterrors(err.response.data)
            }
        }

    }

    var invoiceImg = "./assets/images/avatars/img-8.jpg"
    if (data.invoiceLogo) {
        invoiceImg = IMAGE_URL + data.invoiceLogo
    }
    return (
        <BaseLayout>
            <section className="position-relative p-3 bg-gradient2">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="page-title">
                                <h3 className="my-0">Account Settings</h3>
                                <p className="mt-1 fw-medium">Change your account settings</p>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-2">
                        <div className="col-lg-12">
                            <div className="card">
                                <div className="card-body">
                                    <div className="row">
                                        <div className="col-lg-3">
                                            {/* menu start */}
                                            <SettingSidebar />
                                            {/* menu end */}
                                        </div>
                                        <div className="col-lg-9 invoicesetting">
                                            <div className="tab-content p-0">
                                                {/* account form start */}
                                                <div className="tab-pane fade show active px-3" id="account">
                                                    <h4 className="mt-0">Invoice Information</h4>
                                                    <form onSubmit={onSubmit} className="account-form">
                                                        {/* avatar start */}
                                                        <h6 className="mt-4">Company Logo</h6>
                                                        <div className="row align-items-center">
                                                            <div className="col-auto">
                                                                <input
                                                                    type="file"
                                                                    ref={fileInputRef}
                                                                    style={{ display: 'none' }}
                                                                    onChange={handleFileChange}
                                                                />
                                                                <img
                                                                    src={invoiceImg}
                                                                    className="img-fluid avatar-md rounded-circle shadow"
                                                                    alt="..."
                                                                />
                                                            </div>
                                                            <div className="col">
                                                                <button
                                                                    type="button"
                                                                    onClick={() => fileInputRef.current.click()}
                                                                    className="btn btn-outline-primary btn-sm"
                                                                >
                                                                    Upload
                                                                </button>
                                                                <button
                                                                    type="button"
                                                                    onClick={() => setdata({ ...data, invoiceLogo: "" })}
                                                                    className="btn btn-outline-danger btn-sm ms-2"
                                                                >
                                                                    Remove
                                                                </button>
                                                            </div>
                                                        </div>
                                                        {/* avatar end */}
                                                        <hr className="my-4" />
                                                        {/* basic info start */}
                                                        <div className="row align-items-center">
                                                            <div className="col-lg-6">
                                                                <div className="mb-3">
                                                                    <TextField
                                                                        isRequired
                                                                        label="Name"
                                                                        onChange={(e) => setdata({ ...data, invoiceName: e.target.value })}
                                                                        value={data.invoiceName}
                                                                        placeholder=""
                                                                        error={errors.invoiceName}
                                                                    />
                                                                </div>
                                                                <div className="mb-3">
                                                                    <TextField
                                                                        isRequired
                                                                        label="Email"
                                                                        onChange={(e) => setdata({ ...data, invoiceEmail: e.target.value })}
                                                                        value={data.invoiceEmail}
                                                                        placeholder=""
                                                                        error={errors.invoiceEmail}
                                                                    />
                                                                </div>

                                                            </div>
                                                            <div className="col-lg-6">
                                                                <div className="mb-3">
                                                                    <TextField
                                                                        isRequired
                                                                        label="Phone"
                                                                        onChange={(e) => setdata({ ...data, invoicePhone: e.target.value })}
                                                                        value={data.invoicePhone}
                                                                        placeholder=""
                                                                        error={errors.invoicePhone}
                                                                    />
                                                                </div>
                                                                <div className="mb-3">
                                                                    <TextField
                                                                        isRequired
                                                                        label="Address"
                                                                        onChange={(e) => setdata({ ...data, invoiceAddress: e.target.value })}
                                                                        value={data.invoiceAddress}
                                                                        placeholder=""
                                                                        error={errors.invoiceAddress}
                                                                    />
                                                                </div>
                                                            </div>
                                                        </div>
                                                        {/* account removal end */}
                                                        <hr className="my-4" />
                                                        {/* save start */}
                                                        <div className="row mt-2">
                                                            <div className="col-lg-12">
                                                                <button type="submit" className="btn btn-primary">
                                                                    Save Changes
                                                                </button>
                                                            </div>
                                                        </div>
                                                        {/* save end */}
                                                    </form>
                                                </div>
                                                {/* account form end */}

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </BaseLayout>
    )
}

export default Setting
